import { Iquestion } from './../../types/interface';
import { PayloadAction, createSlice } from "@reduxjs/toolkit";


const namespace = 'test'
const questions: Iquestion[] = [
  {
    question: ' ',
    first: true
  },
  {
    question: ' ',
    options: [{ text: '', value: 1 }, { text: '', value: 2 }]
  },
  {
    question: '    ?',
    options: [{ text: '', value: 1 }, { text: '', value: 2 }]
  },
  {
    question: '   ?',
    options: [{ text: '', value: 1 }, { text: '', value: 2 }]
  },
  {
    question: '   ?',
    options: [{ text: '   ', value: 1 }, { text: ' 2   ', value: 2 }, { text: ' 1   ', value: 3 }, { text: '  2   ', value: 4 }]
  },
  {
    question: '  ?',
    options: [{ text: ' ', value: 1 }, { text: ' ', value: 2 }, { text: ' ', value: 3 }]
  },
  {
    question: '   ?',
    options: [{ text: '', value: 1 }, { text: '', value: 2 }]
  },
  {
    question: '      ?',
    options: [{ text: ' 20:00', value: 1 }, { text: ' 19:00', value: 2 }, { text: ' ', value: 3 }]
  },
  {
    question: '     ?',
    options: [{ text: '3  ', value: 1 }, { text: ' ', value: 2 }, { text: ' , ', value: 3 }, { text: '', value: 4 }, { text: '', value: 5 }]
  },
  {
    question: '      ? ',
    options: [{ text: '  ', value: 1 }, { text: ' ', value: 2 }, { text: '  ', value: 3 }]
  },
  {
    question: '  ?',
    options: [{ text: '', value: 1 }, { text: '', value: 2 }, { text: ' ', value: 3 }],
  },
  {
    question: '  ?',
    options: [{ text: ',   ', value: 1 }, { text: '   30 ', value: 2 }, { text: '  10 ', value: 3 }]
  },
  {
    question: '      ?',
    options: [{ text: '8 ', value: 1 }, { text: '10 ', value: 2 }, { text: '12 ', value: 3 }, { text: '  -   ', value: 4 }]
  },
  {
    question: '   ?',
    options: [{ text: '', value: 1 }, { text: ' ', value: 2 }, { text: ' ', value: 3 }]
  },
  {
    question: '     ?',
    options: [{ text: '    ', value: 1 }, { text: '1   ', value: 2 }, { text: '2   ', value: 3 }, { text: '?   ', value: 4 }]
  },
]

export interface ITest {
  questions: Iquestion[]
  step: number
  answers: number[]
}


const initialState: ITest = {
  questions: questions,
  step: 0,
  answers: [],
}



export const testSlice = createSlice({
  name: namespace,
  initialState: initialState,
  reducers: {
    plusStep: (state, action: PayloadAction<number>) => {
      state.answers = [...state.answers, action.payload]
      if (state.questions.length - 1 === state.step) return
      state.step += 1;
    },
    minusStep: (state) => {
      state.answers.pop()
      state.step -= 1;
    },
    resetSteps: (state) => {
      state.step = 0
      state.answers = []
      state.questions = questions
    },
  },
})

export const { plusStep, minusStep, resetSteps } = testSlice.actions;