import { useNavigate } from 'react-router-dom';
import Button, { ThemeButton } from '../../components/UI/Button/Button';
import styles from './Document.module.scss'
import { useCallback, useState } from 'react';
import CopyNotification from '../../components/Copy/Copy';

const Document = () => {
    const navigate = useNavigate();

    const onBack = useCallback(() => {
        navigate("/");
    }, [navigate])

    const onEmail = () => {
        const link = 'start@freedomcode.ru'
        navigator.clipboard.writeText(link).then(() => { }).catch(() => {
            const textarea = document.createElement('textarea');
            document.body.appendChild(textarea);
            textarea.innerHTML = link;
            textarea.focus();
            textarea.select();
            document.execCommand('copy');
            textarea.remove();
            document.documentElement.scrollTop = 0;
            window.scrollTo(0, 0);

        });

        setIsVisible(true);

        setTimeout(() => {
            setIsVisible(false);
        }, 2000);
    }

    const [isVisible, setIsVisible] = useState(false);


    return (
        <>
             <CopyNotification message="!" isVisible={isVisible} />
            <div className={styles.screen}>
                <div className={styles.block}>
                    <p>
                            
                        <br />
                        <br />
                               152-     27.07.2006  ,                  ,        : 454100, . , . , . 6, . 138 (    ),  1237400005732,  7448249716.
                        <br />
                        <br />
                            ,           .
                        <br />
                        <br />
                                :
                        <br />
                        <br />
                        ;<br />
                        ;<br />
                        ;<br />
                          () ;<br />
                                       /    : , , , ,  (, ), , ,      ,      ,    .
                        <br />
                        <br />
                                   :
                        <br />
                        <br />
                          /;<br />
                            ,   /;<br />
                              ;<br />
                            ,    ,  /// .<br />
                                      <span className={styles.email} onClick={onEmail}>start@freedomcode.ru</span>.                     ,    211  1  6,  2  10   2  11    152-     26.06.2006 
                    </p>
                    <Button theme={ThemeButton.RED} text='' className={styles.btn_first} onClick={onBack} />
                </div>
            </div>
        </>
    )
};

export default Document
